<template>
  <div class="item">
    <input type="text" class="form-control" :id="data.name"  placeholder="" :value="data.value" required>
    <div class="invalid-feedback">必须输入有效的{{data.title}}</div>
  </div>
</template>

<script>
export default {
  props: {
    data:{
      type:Object,
      function(){
        return {};
      }
    }
  },
}
</script>

