<template>
  <form class="needs-validation" >
    <div v-for="item in channels" :key="item.id" class="row">
      <Channel :data="item" />
    </div>
    <button class="w-100 btn btn-primary btn-lg" type="submit">提交</button>
  </form>
</template>

<script>
import Channel from "./Channel";
export default {
  // 注册 channel
  components: {
    Channel,
  },
  data() {
    return {
      channels: [
        { id: 0,name:"lastName",title: "姓",value: "" },
        { id: 1,name:"firstName",title: "名字",value: ""},
      ],
    };
  },
};
</script>

<style>
</style>


