<template>
  <div class="mb-3 row">
    <label :for="data.name" class="col-sm-2 col-form-label">{{data.title}}</label>
    <div class="col-sm-10">
    <Put :data="data"/>
    </div>
  </div>
</template>

<script>
import Put from "./put";
export default {
  props: {
    data:{
      type:Object,
      function(){
        return {};
      }
    }
  },
  components: {
    Put,
  }
};
</script>

<style scoped>
.channel {
  width: 100%;
  height: 40px;
  line-height: 40px;
}
.inner {
  padding: 0 20px;
}
.name {
  font-size: 14px;
  color: #212121;
}
.number {
  font-size: 12px;
  color: #999;
  margin-left: 8px;
}
.name,
.number {
  float: left;
}
</style>


